import "./App.css";
import React, { useState, useEffect } from "react";
import { message } from "antd";
import { PublicClientApplication } from "@azure/msal-browser";
import Header from "./Header";
import Main from "./Main";
import List from "./List";
import config from "./config";

const displayError = (err) => {
  message.error(err);
};

const App = () => {
  let loginTimeoutID = null;
  const [tab, setTab] = useState(0);
  const [authenticated, setAuthenticated] = useState(false);
  const [mobileMode, setMobileMode] = useState(false);
  const [user, setUser] = useState("hahahaha");
  const [idToken, setIdToken] = useState(null);
  const [username, setUsername] = useState(null);
  const [error, setError] = useState(null);
  const [shortURL, setShortURL] = useState(null);

  useEffect(() => {
    if (window.innerWidth < 768) {
      setMobileMode(true);
    } else {
      setMobileMode(false);
    }
    const windowResizeEventListener = window.addEventListener("resize", () => {
      if (window.innerWidth < 768) {
        setMobileMode(true);
      } else {
        setMobileMode(false);
      }
    });
    return () => {
      window.removeEventListener("resize", windowResizeEventListener);
    };
  }, []);

  useEffect(() => {
    if (error) {
      displayError(error);
      setError(null);
    }
  }, [error]);

  const publicClientApplication = new PublicClientApplication({
    auth: {
      clientId: config.appId,
      redirectUri: config.redirectUri,
      authority: config.authority,
    },
  });

  const login = () => {
    try {
      publicClientApplication
        .loginPopup({
          scopes: config.scopes,
          prompt: "select_account",
        })
        .then((res) => {
          loginTimeoutID = setTimeout(logout, 3600000);
          setUser(res.account.name);
          setIdToken(res.idToken);
          setUsername(res.account.username);
          setAuthenticated(true);
        });
    } catch (err) {
      console.log(err);
      setError("Something went wrong! :(");
    }
  };

  const logout = () => {
    try {
      publicClientApplication.logoutPopup().then((res) => {
        clearTimeout(loginTimeoutID);
        setUser(null);
        setAuthenticated(false);
        setTab(0);
      });
    } catch (err) {
      console.log(err);
      setError("Something went wrong! :(");
    }
  };

  const renderTab = () => {
    if (tab === 0) {
      return (
        <Main
          authenticated={authenticated}
          idToken={idToken}
          username={username}
          setError={setError}
          mobileMode={mobileMode}
          shortURL={shortURL}
          setShortURL={setShortURL}
        />
      );
    } else if (tab === 1) {
      return (
        <div
          style={{
            position: "fixed",
            top: "100px",
            margin: "2rem 1rem",
            fontSize: "18px",
            height: "40vh",
          }}
        >
          GLAM URL shortener is a minimalistic link shortening platform built
          specifically for Oxford GLAM by
          <a
            target="_blank"
            rel="noreferrer"
            href="https://www.cloudmantra.net"
          >
            {` cloudmantra.net`}
            <sup>TM</sup>
          </a>
        </div>
      );
    } else if (tab === 2 && authenticated) {
      return <List idToken={idToken} username={username} setError={setError} />;
    } else {
      return null;
    }
  };

  return (
    <div>
      <div>
        <Header
          user={user}
          authenticated={authenticated}
          login={login}
          logout={logout}
          tab={tab}
          setTab={setTab}
        />
        {renderTab()}
      </div>
    </div>
  );
};

export default App;
