import "./Header.css";
import React from "react";
import { Tooltip } from "antd";

const Header = ({ logout, user, login, authenticated, tab, setTab }) => {
  return (
    <div className="header">
      <div>
        <p
          className={tab === 0 ? "active" : "link-inactive"}
          onClick={() => setTab(0)}
        >
          GLAM URL Shortener
        </p>
        <p
          className={tab === 1 ? "active" : "link-inactive"}
          onClick={() => {
            if (tab === 1) {
              setTab(0);
            } else {
              setTab(1);
            }
          }}
        >
          About
        </p>
      </div>
      <div className={authenticated ? "" : "fix-view-div"}>
        {authenticated ? (
          <>
            <p>
              Welcome{" "}
              <Tooltip title="Dashboard">
                <span
                  onClick={() => {
                    if (tab === 2) {
                      setTab(0);
                    } else {
                      setTab(2);
                    }
                  }}
                  className={`header-user-tab ${
                    tab === 2 ? "active" : "inactive"
                  }`}
                >
                  {user}
                </span>
              </Tooltip>
            </p>
            <button className="logout-button" onClick={logout}>
              Sign out
            </button>
          </>
        ) : (
          <button className="login-button fix-view-button" onClick={login}>
            Sign in with AD
          </button>
        )}
      </div>
    </div>
  );
};

export default Header;
